@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
body {
  font-family: 'Poppins', Arial, sans-serif;
  font-size: 16px;
  line-height: 1.8;
  font-weight: normal;
  background: #fafafa;
  color: #666666;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.contact-section {
  padding: 7em 0;
}

.wrapper {
  width: 100%;
  box-shadow: 0px 21px 41px -13px rgba(0, 0, 0, 0.18);
}

.contact-wrap {
  background: #7f279c;
  background: -webkit-gradient(left top, right bottom, color-stop(0%, #7f279c), color-stop(100%, #2e279d));
  background: linear-gradient(135deg, #7f279c 0%, #2e279d 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7f279c', endColorstr='#2e279d', GradientType=1 );
}

.contact-wrap h3 {
  color: #fff;
}

h3 {
  color: white;
}

.img {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.form-control {
  height: 62px;
  background: transparent;
  color: rgba(255, 255, 255, 0.8) !important;
  font-size: 16px;
  border-radius: 0px;
  box-shadow: none !important;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.form-control::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(255, 255, 255, 0.5) !important;
}

.form-control::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(255, 255, 255, 0.5) !important;
}

.form-control:-ms-input-placeholder {
  /* IE 0+ */
  color: rgba(255, 255, 255, 0.5) !important;
}

.form-control:-moz-placeholder {
  /* Firefox 18- */
  color: rgba(255, 255, 255, 0.5) !important;
}

.form-control:focus, .form-control:active {
  background: transparent;
  border-color: rgba(255, 255, 255, 0.5) !important;
}

textarea.form-control {
  height: inherit !important;
  margin-top: 5px;
}

.contactForm .form-control {
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0;
}

.btn {
  padding: 12px 16px;
  cursor: pointer;
  border-width: 1px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
  box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  position: relative;
  margin-top: 20px;
}

.btn-primary {
  background: #d62196 !important;
  border-color: #d62196 !important;
  color: #fff;
}

.info-wrap h3 {
  color: #000;
  position: relative;
}

.info-wrap h3:after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background: #d62196;
  content: '';
}

.info-wrap .dbox {
  width: 100%;
  margin-bottom: 25px;
}

.info-wrap .dbox:last-child() {
  margin-bottom: 0;
}

.info-wrap .dbox p {
  margin-bottom: 0;
}

.info-wrap .dbox p span {
  font-weight: 400;
  color: #000;
}

.info-wrap .dbox p a {
  color: rgba(0, 0, 0, 0.3);
}

.info-wrap .dbox .icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.info-wrap .dbox .icon span {
  font-size: 20px;
  color: #000;
}

.info-wrap .dbox .text {
  width: calc(100% - 50px);
}

h1,
h2,
h3,
h4,
h5,
.h1,
.h2,
.h3,
.h4,
.h5 {
  line-height: 1.5;
  font-weight: 400;
  font-family: 'Poppins', Arial, sans-serif;
  color: #000;
}

a {
  transition: 0.3s all ease;
  color: #d62196;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none !important;
  outline: none !important;
  box-shadow: none;
}

#contactForm .form-control {
  font-size: 16px;
}
